export default {
  namespaced: true,
  state: {
    // 
    value: '' // medium small mini
  },
  mutations: {
    /**
     * @description 
     * @param {Object} state vuex state
     * @param {String} size 
     */
    set (state, size) {
      // store 
      state.value = size
      // 
      this.dispatch('d2admin/db/set', {
        dbName: 'sys',
        path: 'size.value',
        value: state.value,
        user: true
      })
    },
    /**
     * @description 
     * @param {Object} state vuex state
     */
    async load (state) {
      // store 
      state.value = await this.dispatch('d2admin/db/get', {
        dbName: 'sys',
        path: 'size.value',
        defaultValue: 'default',
        user: true
      })
    }
  }
}
